import React from 'react';

const isValidLink = (url) => {
    try {
        new URL(url);
        return true;
    } catch (err) {
        console.log("ERR >> Invalid Link ", url);
        return false;
    }
}

const ItemLink = ({ url, fullLink }) => {
    const getLink = (url, fullLink, encodeURI) => {
        if (fullLink) {
            // let fileName = url.split('/').pop();
            // let encodedFileName = encodeURIComponent(fileName);
            // return url.replace(fileName, encodedFileName);
            let str = url;
            str = str.replaceAll(`[`, `%5B`);
            str = str.replaceAll(`]`, `%5D`);
            str = str.replaceAll(`)`, `%29`);
            str = str.replaceAll(`(`, `%28`);
            return str;
        } else if (encodeURI) {
            let str = url;
            let fileName = str.split('/').pop();
            let encodedFileName = encodeURIComponent(fileName);
            encodedFileName = encodedFileName.replaceAll(`)`, `%29`);
            encodedFileName = encodedFileName.replaceAll(`(`, `%28`);
            str = str.replace(fileName, encodedFileName);
            return str;
        } 
        else {
            return url.split('/').pop();
        }
    }
    return isValidLink(url) && (<a href={getLink(url, true, false)} target="_blank" rel="noreferrer" className="ArchiveLinkTitle">{url}</a>);
}

export default ItemLink;