import './App.css';
import Home from './components/Home/Home';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  useParams
} from "react-router-dom";
import './DarkMode.css';

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/page/:id" children={<Home />} />
          <Route
            exact
            path="/"
            render={() => {
              return (
                <Redirect to="/page/1" />
              )
            }}
          />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
